import * as React from 'react'
import { Link, useStaticQuery, graphql } from 'gatsby'

const Layout = ({ pageTitle, children }) => {
  const data = useStaticQuery(graphql`
   query {
     site {
       siteMetadata {
         title
         tagline
         author
       }
     }
   }
  `)
console.log(data.site.siteMetadata)
  return (
    <div>
      <title>{pageTitle} | {data.site.siteMetadata.title}</title>
      <header className="pt-2 pb-2 text-white bg-gradient-to-br from-teal-600 to-zinc-800">
        <div className="mr-4 text-sm text-right uppercase space-x-2">
          <Link to="/about">About</Link>
          {/* <Link>Licenses</Link> */}
        </div>
        <div className="mb-4 max-w-4xl mx-auto p-3 text-center">
          <Link to="/" className="font-serif text-6xl font-bold">{data.site.siteMetadata.title}</Link>
          <div className="italic text-xs text-gray-300">{data.site.siteMetadata.tagline} by {data.site.siteMetadata.author}</div>
        </div>
      </header>
      <main className="min-w-fit flex-auto m-2 space-y-4">
        {children}
      </main>
    </div>
  )
}

export default Layout
